import navBar from '@/components/navbar/navbar.vue'
import tabarNav from '@/components/tabar/tabar.vue'
import uniCombox from '../../components/uni-combox/uni-combox.vue'
import api from '@/api/api.js'
export default {
	data() {
		return {
			navText:'发布',
			replyValue:'',
			title:'',
			typeItem:'',
			types:['招聘','问答','分享'],
			showSelector:false
		};
	},
	components:{
		navBar,
		uniCombox
	},
	methods:{
		Changetype(e){
			 this.typeItem = e.target.dataset.value
			
			 this.showSelector = !this.showSelector
		},
		async subReply(){
			let item;
			switch(true){
				case this.typeItem =="招聘":
					item ="job"
					break;
				case this.typeItem =="分享":
					item ="share"
					break;
				case this.typeItem =="问答":
					item ="ask"
					break			
			}
			const [error,data] = await api.topics({
				method:'post',
				query:{
					title :this.title,
					tab:item,
					content:this.replyValue
				}
			})
			if(data.data.success){
				this.replyValue = '';
				this.$Tips.alert('提交成功');
			}else{
				this.$Tips.alert(data.data.error_msg);
			}
		},
		onInput(){
			this.showSelector = !this.showSelector
		}
	}
}
